@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.page {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}

.page__body {
  margin: 0;
}

.page-main__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.page-main__subtitle {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.catalog {
  padding: 20px;
}

.catalog__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  padding-left: 20px;
  padding-top: 26px;
  padding-bottom: 42px;
  margin: 0;
}

.field-group {
  border: none;
}

.field-group__list {
  list-style: none;
}

.field__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}

.field__input {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}

.control__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #222222;
  text-transform: uppercase;
}

.field-group__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
}

.checkbox__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.form__remark {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
}

.main-nav {
  text-align: center;
}
.main-nav__list {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  list-style: none;
}
.main-nav__link {
  color: #000000;
  text-decoration: none;
}

.program-list {
  padding: 20px;
}

.program-list__item {
  background-color: #f2f2f2;
  padding-left: 20px;
}

.program-list__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
  font-weight: 400;
}

.program-list__description {
  font-family: "Arial", sans-serif;
  color: #444444;
}

.program-list__link {
  text-decoration: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}

.card__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: #222222;
}

.card__link {
  text-decoration: none;
}

.card__property {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.card__description {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.process {
  padding: 20px;
}

.process__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin: 0;
}

.process__list {
  list-style: none;
  font-family: "Arial", sans-serif;
  color: #444444;
}

.process__item:not(:last-child) {
  padding-bottom: 20px;
}

.example {
  padding: 25px 25px 40px;
  background-color: #eaeaea;
}

.example__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin: 0;
}

.example__text {
  font-family: "Arial", sans-serif;
  color: #444444;
}

.example__item {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
  text-transform: lowercase;
}

.example__description {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
}

.example__spending {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}

.example__toggle {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.footer {
  background-color: #f2f2f2;
}

.footer__adress {
  padding: 26px 20px;
}

.footer--white {
  background-color: #ffffff;
}

.footer__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  text-transform: uppercase;
}

.footer__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}

.footer-social {
  padding: 40px 20px 22px;
  text-align: center;
}

.footer__developer {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  color: #444444;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.promo {
  color: #ffffff;
  background-image: url("../../img/background/cat-promo.jpg");
  background-color: #68b738;
  padding: 20px;
  text-align: center;
  position: relative;
  z-index: 0;
}
.promo::after {
  content: "";
  background-color: #68b738;
  opacity: 0.8;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.promo__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.promo__slogan {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 14px;
  text-transform: uppercase;
}

.button {
  text-decoration: none;
  color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  background-color: #68b738;
  border: none;
}

.promo__button {
  line-height: 18px;
  padding: 7px 44px;
}

.show-more {
  padding: 20px;
}

.show-more__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}

.show-more__text {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.show-more__button {
  background-color: #f2f2f2;
  color: #444444;
}

.additional {
  padding: 20px;
}

.additional__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.additional__list {
  list-style: none;
}

.additional__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}

.card-additional__description {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.offer {
  position: relative;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  background-image: url("../../img/background/cat-additional-offer.jpg");
  background-color: #68b738;
  padding: 60px 60px 40px 60px;
  color: #ffffff;
  z-index: 0;
}
.offer::after {
  content: "";
  position: absolute;
  background-color: #68b738;
  opacity: 0.8;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}